.burger-btn {
    padding: 0;
    border: none;
    background-color: transparent;
    cursor: pointer;
  }
  .burger-btn__icon {
    width: 20px;
    height: 12px;
  }
  @media screen and (min-width: 768px) {
    .burger-btn {
      margin-left: auto;
      margin-right: 30px;
    }
  }
  @media screen and (min-width: 1280px) {
    .burger-btn {
      display: none;
    }
  }

.modal-btn {
    display: flex;
    justify-content: center;
    align-items: center;
  
    padding: 0;
    border: none;
  
    min-width: 160px;
    height: 44px;
  
    font-family: inherit;
    font-weight: 700;
    font-size: 16px;
    line-height: 1.3;
  
    cursor: pointer;
    background-color: #fff;
    color: $colorFont-red;
    border-radius: 22px;
  
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  
    &:hover,
    &:focus {
      background-color: $trans-duration $timing-function;
    }
  }

  .modal-btn__icon {
  width: 6px;
  height: 10px;
  margin-left: 12px;
}

@media screen and (max-width: 767px) {
    .modal-btn {
      display: none;
    }
  }
  @media screen and (min-width: 1280px) {
    .modal-btn {
      margin-left: auto;
    }
  }

.nav {
  display: flex;
  align-items: center;
}
.nav-list {
  display: flex;
}

@media screen and (max-width: 1279px) {
  .nav-list {
    display: none;
  }
}

// MOBILE MENU

.mobile-menu {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100vw;
  max-width: 320px;
  height: 100vh;
  transform: translateX(250%);
  margin: auto;

  display: flex;
  flex-direction: column;
  padding-top: 60px;
  background-color: $pink-mobile-menu-background;

  transition: transform $trans-duration $timing-function;
  z-index: 100;
  @media screen and (min-width: $desktop){
    display: none;
    pointer-events: none;
  }
}

.is-open {
  transform: translateX(0);
}

.mobile-menu__close-btn {
  position: absolute;
  top: 23px;
  right: 28px;
  padding: 0;
  border: none;
  background-color: transparent;
}

.mobile-menu__close-btn-icon {
  width: 14px;
  height: 14px;
}

.mobile-menu__modal-btn {
  margin-bottom: 32px;

  min-width: 209px;
  height: 44px;
  padding: 0;
  border: none;
  background-color: #fff;
  border-radius: 22px;

  font-weight: 700;
  font-size: 16px;
  line-height: 1.3;

  align-self: center;
  color: $buttonColor-light-red;
}

.mobile-menu__modal-btn-icon {
    width: 6px;
    height: 10px;
    margin-left: 12px;
}